.popup {
    visibility: hidden;
    position: fixed;
    display: flex;
    background: rgb(0, 0, 0, 0);
    opacity: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    top: 0;
    left: 0;
    -webkit-transition: all 0.3s linear;
    -moz-transition: all 0.3s linear;
    -o-transition: all 0.3s linear;
    transition: all 0.3s linear;
  }
  